import React, { useState, useEffect } from 'react';
import './LifeGraphHome.css';
import {
  Container,
  Box,
  AppBar,
  Toolbar,
  Typography,
  Button,
  Card,
  CardContent,
  IconButton,
  Stack,
  useTheme
} from '@mui/material';
import {
  Home as HomeIcon,
  Save as SaveIcon,
  Upload as UploadIcon,
  Settings as SettingsIcon,
  AutoAwesome as AutoAwesomeIcon,
  Edit as EditIcon,
  Sync as SyncIcon
} from '@mui/icons-material';
import EventInputForm from './EventInputForm';
import GraphDisplay from './GraphDisplay';

function LifeGraphHome() {
  const [currentView, setCurrentView] = useState('home');
  const [events, setEvents] = useState([]);
  const [loadFont, setLoadFont] = useState(false);
  const [loadedFiles, setLoadedFiles] = useState([]);
  const [graphDatasets, setGraphDatasets] = useState([]);

  //     useEffect
  useEffect(() => {
    //   
    const font = new FontFace('NanumGothic', 'url(https://fonts.gstatic.com/ea/nanumgothic/v5/NanumGothic-Regular.woff2) format("woff2")');
    
    font.load().then(() => {
      //     
      document.fonts.add(font);
      setLoadFont(true);
    }).catch(err => {
      console.error('  :', err);
      //      
      setLoadFont(true);
    });
  }, []);

  //    
  const navigateToManualInput = () => {
    setCurrentView('manualInput');
  };

  //    (   )
  const navigateToIntegratedView = () => {
    setCurrentView('integratedView');
  };

  //    (  )
  const navigateToGraphView = (eventData) => {
    setEvents(eventData);
    setCurrentView('graphView');
  };

  //   
  const navigateToHome = () => {
    setCurrentView('home');
  };

  //   
  const handleAddEvent = (newEvent) => {
    setEvents([...events, newEvent]);
  };

  //    
  const handleUpdateEvents = (updatedEvents) => {
    setEvents(updatedEvents);
  };

  //   
  const handleSaveEvents = () => {
    try {
      const userName = localStorage.getItem('userName') || '';
      const now = new Date();
      const dateStr = now.toISOString().slice(0, 10).replace(/-/g, '');
      const timeStr = now.toTimeString().slice(0, 8).replace(/:/g, '');
      
      //   JSON  
      const jsonData = JSON.stringify(events, null, 2);
      
      // Blob  
      const blob = new Blob([jsonData], { type: 'application/json' });
      
      //   
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = `${userName}__${dateStr}_${timeStr}.json`;
      
      //   
      document.body.appendChild(a);
      a.click();
      
      // 
      setTimeout(() => {
        document.body.removeChild(a);
        URL.revokeObjectURL(url);
      }, 100);
      
      alert(' .');
    } catch (error) {
      console.error('   :', error);
      alert('   .');
    }
  };

  //   
  const handleLoadEvents = (e) => {
    const file = e.target.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onload = (event) => {
      try {
        const loadedEvents = JSON.parse(event.target.result);
        const fileName = file.name.split('_')[0];
        
        //   (   )
        if (!localStorage.getItem('userName')) {
          localStorage.setItem('userName', fileName);
        }

        //  
        const newDataset = {
          label: `${fileName} `,
          data: loadedEvents,
          borderColor: '#4e54c8',
          borderWidth: 2,
          tension: 0.4,
          pointRadius: 6,
          pointHoverRadius: 8
        };
        
        setGraphDatasets([...graphDatasets, newDataset]);
        setEvents(loadedEvents);
        
        alert('  .');
        //   
        setCurrentView('integratedView');
      } catch (error) {
        console.error('  :', error);
        alert('   .');
      }
    };
    reader.readAsText(file);
  };

  //    -     ()
  const handleButtonClick = (callback) => {
    //  API     
    if (navigator.vibrate) {
      navigator.vibrate(50); // 50ms 
    }
    
    //  
    callback();
  };

  //   
  const IntegratedView = () => {
    return (
      <Box sx={{ height: '100vh', display: 'flex', flexDirection: 'column' }}>
        <AppBar position="static" color="primary" elevation={0}>
          <Toolbar>
            <Typography variant="h6" component="h1" sx={{ flexGrow: 1 }}>
              
            </Typography>
            <Button
              startIcon={<HomeIcon />}
              color="inherit"
              onClick={() => handleButtonClick(navigateToHome)}
            >
              
            </Button>
            <Button
              startIcon={<SaveIcon />}
              color="inherit"
              onClick={() => handleButtonClick(handleSaveEvents)}
              disabled={events.length === 0}
            >
              
            </Button>
            <Button
              component="label"
              startIcon={<UploadIcon />}
              color="inherit"
            >
              
              <input
                type="file"
                hidden
                accept=".json"
                onChange={handleLoadEvents}
              />
            </Button>
          </Toolbar>
        </AppBar>

        <Box
          sx={{
            display: 'flex',
            flex: 1,
            gap: 2,
            p: 2,
            width: '90%',
            margin: '0 auto'
          }}
        >
          <Card sx={{ flex: 1 }}>
            <CardContent>
              <EventInputForm
                events={events}
                onAddEvent={handleAddEvent}
                onUpdateEvents={handleUpdateEvents}
                isEmbedded={true}
              />
            </CardContent>
          </Card>
          <Card sx={{ flex: 3 }}>
            <CardContent>
              <GraphDisplay
                events={events}
                isEmbedded={true}
              />
            </CardContent>
          </Card>
        </Box>
      </Box>
    );
  };

  //    
  if (!loadFont) {
    //    
    return (
      <Box
        sx={{
          height: '100vh',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center'
        }}
      >
        <Typography variant="h5"> ...</Typography>
      </Box>
    );
  }
  
  if (currentView === 'manualInput') {
    return <EventInputForm 
      onBack={navigateToHome} 
      onSubmit={navigateToGraphView}
      events={events}
      onUpdateEvents={handleUpdateEvents}
    />;
  }
  
  if (currentView === 'graphView') {
    return <GraphDisplay 
      events={events} 
      onBack={() => handleButtonClick(() => setCurrentView('manualInput'))} 
    />;
  }

  if (currentView === 'integratedView') {
    return <IntegratedView />;
  }

  return (
    <Container maxWidth="md" sx={{ py: 4 }}>
      <Box sx={{ mb: 4, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
          <img src="/logo.png" alt=" " style={{ height: 60 }} />
          <Typography variant="h4" component="h1">
            
          </Typography>
        </Box>
        <IconButton
          onClick={() => handleButtonClick(() => alert('   .'))}
          size="large"
        >
          <SettingsIcon />
        </IconButton>
      </Box>

      <Stack spacing={2}>
        <Button
          variant="contained"
          size="large"
          startIcon={<AutoAwesomeIcon />}
          onClick={() => handleButtonClick(() => alert('AI      .'))}
          sx={{ py: 2 }}
        >
           
        </Button>
        
        <Button
          variant="contained"
          size="large"
          startIcon={<EditIcon />}
          onClick={() => handleButtonClick(navigateToManualInput)}
          sx={{ py: 2 }}
        >
            
        </Button>
        
        <Button
          variant="contained"
          size="large"
          startIcon={<SyncIcon />}
          onClick={() => handleButtonClick(navigateToIntegratedView)}
          sx={{ py: 2 }}
        >
            
        </Button>
      </Stack>

      <Box sx={{ mt: 4, textAlign: 'center' }}>
        <Typography variant="subtitle1" color="text.secondary">
             : "    "
        </Typography>
      </Box>
    </Container>
  );
}

export default LifeGraphHome; 