import React, { useState, useEffect, useRef, useMemo, useCallback } from 'react';
import './EventInputForm.css';
import GraphDisplay from './GraphDisplay';
import {
  Box,
  TextField,
  Button,
  IconButton,
  Typography,
  Stack,
  Grid,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  Tooltip
} from '@mui/material';
import {
  Add as AddIcon,
  Delete as DeleteIcon,
  Edit as EditIcon,
  ArrowBack as ArrowBackIcon
} from '@mui/icons-material';

const EMOTIONS = [
  { label: ' ', value: 'veryHappy', emoji: '', color: '#8B5CF6' },
  { label: '', value: 'happy', emoji: '', color: '#3B82F6' },
  { label: '', value: 'neutral', emoji: '', color: '#10B981' },
  { label: '', value: 'sad', emoji: '', color: '#F59E0B' },
  { label: ' ', value: 'verySad', emoji: '', color: '#1E40AF' }
];

function EventInputForm({ onBack, onSubmit, events: initialEvents, onAddEvent, onUpdateEvents, isEmbedded = false }) {
  const [events, setEvents] = useState(initialEvents || []);
  const [name, setName] = useState(localStorage.getItem('userName') || '');
  const [year, setYear] = useState(new Date().getFullYear());
  const [text, setText] = useState('');
  const [selectedEmotion, setSelectedEmotion] = useState('neutral');
  const [selectedColor, setSelectedColor] = useState(EMOTIONS[2].color);
  const [editingIndex, setEditingIndex] = useState(-1);
  const [isEventAdded, setIsEventAdded] = useState(false);
  const yearInputRef = useRef(null);
  const [showGraphModal, setShowGraphModal] = useState(false);
  const graphModalRef = useRef(null);
  const [isNameSaved, setIsNameSaved] = useState(!!localStorage.getItem('userName'));

  const sortedEvents = useMemo(() => {
    return [...events].sort((a, b) => a.year - b.year);
  }, [events]);
  
  useEffect(() => {
    if (initialEvents) {
      setEvents(initialEvents);
    }
  }, [initialEvents]);
  
  useEffect(() => {
    if (yearInputRef.current) {
      yearInputRef.current.focus();
    }
  }, []);
  
  useEffect(() => {
    if (isEventAdded) {
      const timer = setTimeout(() => {
        setIsEventAdded(false);
      }, 3000);
      return () => clearTimeout(timer);
    }
  }, [isEventAdded]);
  
  useEffect(() => {
    const handleEscKey = (e) => {
      if (e.key === 'Escape' && showGraphModal) {
        setShowGraphModal(false);
      }
    };
    
    document.addEventListener('keydown', handleEscKey);
    return () => {
      document.removeEventListener('keydown', handleEscKey);
    };
  }, [showGraphModal]);
  
  useEffect(() => {
    const handleOutsideClick = (e) => {
      if (showGraphModal && graphModalRef.current && !graphModalRef.current.contains(e.target)) {
        setShowGraphModal(false);
      }
    };
    
    document.addEventListener('mousedown', handleOutsideClick);
    return () => {
      document.removeEventListener('mousedown', handleOutsideClick);
    };
  }, [showGraphModal]);
  
  useEffect(() => {
    if (isNameSaved) {
      document.title = `${name} `;
    }
  }, [isNameSaved, name]);
  
  //     
  useEffect(() => {
    const handleUserNameUpdate = (e) => {
      const newName = e.detail;
      setName(newName);
      setIsNameSaved(true);
    };
    
    window.addEventListener('userNameUpdate', handleUserNameUpdate);
    return () => {
      window.removeEventListener('userNameUpdate', handleUserNameUpdate);
    };
  }, []);

  //     
  useEffect(() => {
    const handleGraphDataUpdate = (e) => {
      const newEvents = e.detail;
      setEvents(newEvents);
    };
    
    window.addEventListener('graphDataUpdate', handleGraphDataUpdate);
    return () => {
      window.removeEventListener('graphDataUpdate', handleGraphDataUpdate);
    };
  }, []);

  //    
  useEffect(() => {
    const handleResetForm = () => {
      setYear(new Date().getFullYear());
      setText('');
      setSelectedEmotion('neutral');
      setSelectedColor(EMOTIONS[2].color);
      setEditingIndex(-1);
    };
    
    window.addEventListener('resetForm', handleResetForm);
    return () => {
      window.removeEventListener('resetForm', handleResetForm);
    };
  }, []);

  const handleNameSave = useCallback(() => {
    if (name.trim()) {
      localStorage.setItem('userName', name);
      setIsNameSaved(true);
      const nameField = document.getElementById('name-field');
      if (nameField) {
        nameField.style.animation = 'saveSuccess 0.5s ease';
      }
    }
  }, [name]);

  const handleNameReset = useCallback(() => {
    setName('');
    localStorage.removeItem('userName');
    setIsNameSaved(false);
    //    
    setYear(new Date().getFullYear());
    setText('');
    setSelectedEmotion('neutral');
    setSelectedColor(EMOTIONS[2].color);
    setEditingIndex(-1);
    setEvents([]);
    if (onUpdateEvents) {
      onUpdateEvents([]);
    }
  }, [onUpdateEvents]);
  
  const handleSubmit = useCallback((e) => {
    e.preventDefault();
    
    if (!isNameSaved) {
      alert('  .');
      return;
    }
    
    if (!year || !text) {
      alert('    .');
      return;
    }
    
    const newEvent = {
      name,
      year: parseInt(year),
      description: text,
      emotion: selectedEmotion,
      color: selectedColor
    };
    
    if (editingIndex === -1) {
      const updatedEvents = [...events, newEvent].sort((a, b) => a.year - b.year);
      setEvents(updatedEvents);
      if (onUpdateEvents) {
        onUpdateEvents(updatedEvents);
      }
    } else {
      const updatedEvents = [...events];
      updatedEvents[editingIndex] = newEvent;
      setEvents(updatedEvents.sort((a, b) => a.year - b.year));
      if (onUpdateEvents) {
        onUpdateEvents(updatedEvents);
      }
      setEditingIndex(-1);
    }
    
    setIsEventAdded(true);
    localStorage.setItem('lastUsedYear', year.toString());
    setText('');
    setSelectedEmotion('neutral');
    setSelectedColor(EMOTIONS[2].color);
  }, [events, year, text, name, selectedEmotion, selectedColor, editingIndex, isNameSaved, onUpdateEvents]);
  
  const handleViewGraph = useCallback(() => {
    if (events.length === 0) {
      alert('   .');
      return;
    }
    onSubmit(sortedEvents);
  }, [events, onSubmit, sortedEvents]);
  
  const openGraphModal = () => {
    if (events.length === 0) {
      alert('   .');
      return;
    }
    setShowGraphModal(true);
  };
  
  const closeGraphModal = () => {
    setShowGraphModal(false);
  };
  
  const exportAsImage = (format) => {
    const canvas = document.querySelector('.graph-canvas');
    if (!canvas) {
      alert('   .');
      return;
    }
    
    try {
      const dataUrl = canvas.toDataURL(`image/${format}`);
      
      const a = document.createElement('a');
      a.href = dataUrl;
      a.download = `_${new Date().toISOString().slice(0, 10)}.${format}`;
      document.body.appendChild(a);
      a.click();
      
      setTimeout(() => {
        document.body.removeChild(a);
      }, 100);
      
      alert(` ${format.toUpperCase()}   .`);
    } catch (error) {
      console.error('   :', error);
      alert('   .');
    }
  };
  
  const handleDelete = useCallback((index) => {
    const updatedEvents = events.filter((_, i) => i !== index);
    onUpdateEvents(updatedEvents);
  }, [events, onUpdateEvents]);
  
  //     
  const handleEdit = useCallback((index) => {
    const event = events[index];
    setYear(event.year);
    setText(event.description);
    setSelectedEmotion(event.emotion);
    setSelectedColor(event.color);
    setEditingIndex(index);
  }, []);
  
  //     
  const getEmotionColor = (emotion) => {
    const emotionObj = EMOTIONS.find(e => e.value === emotion);
    return emotionObj ? emotionObj.color : '#868e96'; //  - 
  };
  
  //   
  const handleKeyDown = useCallback((e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSubmit(e);
    }
  }, [handleSubmit]);
  
  //     UI
  const compactStyles = isEmbedded ? {
    container: { minHeight: 'auto', maxHeight: '100%', overflow: 'auto' },
    header: { padding: '0.5rem 0.75rem' },
    form: { padding: '0.5rem 0.75rem' }
  } : {};
  
  return (
    <Box sx={{ 
      height: '100%', 
      display: 'flex', 
      flexDirection: 'column',
      bgcolor: 'transparent',
      borderRadius: 1,
      p: 2,
      '& .MuiGrid-root': {
        gap: '10px'
      },
      '& .MuiButton-root': {
        background: 'linear-gradient(135deg, #4e54c8 0%, #8f94fb 100%)',
        color: '#FFFFFF',
        '&:hover': {
          background: 'linear-gradient(135deg, #4448a9 0%, #7a7fe6 100%)',
        }
      },
      '& .MuiTextField-root.memory-input': {
        width: '110%',
        '& .MuiInputBase-root': {
          height: '90%'
        }
      },
      '& .emotion-container': {
        display: 'flex',
        flexDirection: 'row',
        gap: '8px',
        flexWrap: 'nowrap',
        overflowX: 'auto',
        padding: '8px 0'
      }
    }}>
      {!isEmbedded && (
        <Box sx={{ mb: 2 }}>
          <Button
            startIcon={<ArrowBackIcon />}
            onClick={onBack}
            variant="outlined"
            color="primary"
          >
            
          </Button>
        </Box>
      )}

      <Typography variant="h6" component="h2" sx={{ mb: 3 }}>
        {isNameSaved ? `${name} ` : ' '}
      </Typography>

      <form onSubmit={handleSubmit} style={compactStyles.form}>
        <Grid container spacing={1}>
          <Grid item xs={12} sm={6}>
            <TextField
              id="name-field"
              fullWidth
              label=""
              value={name}
              onChange={(e) => setName(e.target.value)}
              disabled={isNameSaved}
              required
              size="medium"
              InputProps={{
                endAdornment: (
                  <Box sx={{ display: 'flex', gap: 1 }}>
                    {!isNameSaved && (
                      <Button 
                        onClick={handleNameSave} 
                        size="small"
                        sx={{
                          minWidth: 'auto',
                          px: 2,
                          whiteSpace: 'nowrap'
                        }}
                      >
                        
                      </Button>
                    )}
                    {isNameSaved && (
                      <Button 
                        onClick={handleNameReset}
                        size="small"
                        sx={{
                          minWidth: 'auto',
                          px: 2,
                          whiteSpace: 'nowrap'
                        }}
                      >
                        
                      </Button>
                    )}
                  </Box>
                ),
                style: { 
                  fontSize: '1rem',
                  backgroundColor: '#ffffff',
                  color: '#333333'
                }
              }}
              sx={{
                '& .MuiInputBase-input': {
                  color: '#333333',
                  backgroundColor: '#ffffff'
                },
                '& .MuiInputLabel-root': {
                  color: '#666666'
                },
                '& .MuiOutlinedInput-root': {
                  '& fieldset': {
                    borderColor: 'rgba(78, 84, 200, 0.2)'
                  },
                  '&:hover fieldset': {
                    borderColor: 'rgba(78, 84, 200, 0.4)'
                  },
                  '&.Mui-focused fieldset': {
                    borderColor: '#4e54c8'
                  }
                }
              }}
            />
          </Grid>
          <Grid item xs={12} sm={6}>
            <TextField
              fullWidth
              label=""
              type="number"
              value={year}
              onChange={(e) => {
                const newYear = parseInt(e.target.value);
                setYear(newYear);
                localStorage.setItem('lastUsedYear', newYear.toString());
              }}
              required
              size="medium"
              InputProps={{
                style: { 
                  fontSize: '1rem',
                  backgroundColor: '#ffffff',
                  color: '#333333'
                }
              }}
              sx={{
                '& .MuiInputBase-input': {
                  color: '#333333',
                  backgroundColor: '#ffffff'
                },
                '& .MuiInputLabel-root': {
                  color: '#666666'
                },
                '& .MuiOutlinedInput-root': {
                  '& fieldset': {
                    borderColor: 'rgba(78, 84, 200, 0.2)'
                  },
                  '&:hover fieldset': {
                    borderColor: 'rgba(78, 84, 200, 0.4)'
                  },
                  '&.Mui-focused fieldset': {
                    borderColor: '#4e54c8'
                  }
                }
              }}
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              className="memory-input"
              fullWidth
              label=""
              value={text}
              onChange={(e) => setText(e.target.value)}
              onKeyDown={handleKeyDown}
              required
              multiline
              rows={4}
              sx={{ 
                '& .MuiInputBase-input': {
                  fontSize: '1.1rem',
                  lineHeight: '1.6'
                }
              }}
              placeholder="  (Enter )"
            />
          </Grid>
          <Grid item xs={12}>
            <Typography variant="subtitle1" sx={{ mb: 1, fontWeight: 500 }}> </Typography>
            <div className="emotion-container">
              {EMOTIONS.map((emotion) => (
                <Tooltip 
                  key={emotion.value} 
                  title={emotion.label}
                  arrow
                  placement="top"
                >
                  <IconButton
                    onClick={() => {
                      setSelectedEmotion(emotion.value);
                      setSelectedColor(emotion.color);
                    }}
                    sx={{
                      width: 48,
                      height: 48,
                      flexShrink: 0,
                      bgcolor: selectedEmotion === emotion.value ? emotion.color : 'transparent',
                      color: selectedEmotion === emotion.value ? 'white' : 'inherit',
                      '&:hover': {
                        bgcolor: emotion.color,
                        color: 'white',
                        transform: 'scale(1.1)',
                      },
                      border: `2px solid ${emotion.color}`,
                      transition: 'all 0.3s ease',
                      fontSize: '1.5rem'
                    }}
                  >
                    {emotion.emoji}
                  </IconButton>
                </Tooltip>
              ))}
            </div>
          </Grid>
        </Grid>

        <Box sx={{ 
          mt: 4, 
          display: 'flex', 
          justifyContent: 'flex-end',
          gap: 2
        }}>
          <Button
            type="submit"
            variant="contained"
            startIcon={<AddIcon />}
            size="large"
            sx={{
              bgcolor: selectedColor,
              px: 4,
              py: 1.5,
              '&:hover': {
                bgcolor: selectedColor,
                filter: 'brightness(0.9)',
              },
              transition: 'all 0.3s ease'
            }}
          >
            {editingIndex === -1 ? '' : ''}
          </Button>
        </Box>
      </form>

      <Box sx={{ mt: 3, flex: 1, overflow: 'auto' }}>
        <Typography variant="subtitle1" sx={{ mb: 1 }}>
          {events.length}  
        </Typography>
        <List>
          {events.map((event, index) => {
            const emotion = EMOTIONS.find(e => e.value === event.emotion);
            return (
              <ListItem
                key={index}
                sx={{
                  bgcolor: 'background.paper',
                  mb: 1,
                  borderRadius: 1,
                  border: 1,
                  borderColor: 'rgba(78, 84, 200, 0.2)',
                  position: 'relative',
                  '&::before': {
                    content: '""',
                    position: 'absolute',
                    left: 0,
                    top: 0,
                    bottom: 0,
                    width: '6px',
                    backgroundColor: EMOTIONS.find(e => e.value === event.emotion)?.color || '#ccc',
                    borderTopLeftRadius: '4px',
                    borderBottomLeftRadius: '4px'
                  },
                  '&:hover': {
                    bgcolor: 'rgba(78, 84, 200, 0.05)'
                  },
                  '& .MuiTypography-root': {
                    color: '#333333'
                  },
                  '& .MuiListItemSecondaryAction-root': {
                    '& .MuiIconButton-root': {
                      color: '#4e54c8'
                    }
                  }
                }}
              >
                <ListItemText
                  primary={
                    <Typography variant="subtitle2">
                      {event.year} {emotion?.emoji}
                    </Typography>
                  }
                  secondary={event.description}
                />
                <ListItemSecondaryAction>
                  <IconButton
                    edge="end"
                    onClick={() => handleEdit(index)}
                    sx={{ mr: 1 }}
                  >
                    <EditIcon />
                  </IconButton>
                  <IconButton
                    edge="end"
                    onClick={() => handleDelete(index)}
                  >
                    <DeleteIcon />
                  </IconButton>
                </ListItemSecondaryAction>
              </ListItem>
            );
          })}
        </List>
      </Box>

      {events.length > 0 && (
        <div className="graph-buttons">
          <button
            type="button"
            className="view-graph-button"
            onClick={openGraphModal}
            aria-label=" "
          >
            <span className="button-icon"></span>
            <span> </span>
          </button>
          <button
            type="button"
            className="download-button"
            onClick={() => exportAsImage('png')}
            disabled={events.length === 0}
            aria-label=""
          >
            <span className="button-icon"></span>
            <span></span>
          </button>
        </div>
      )}
      
      {/*    */}
      {showGraphModal && (
        <div className="graph-modal-overlay">
          <div className="graph-modal-content" ref={graphModalRef}>
            <div className="graph-modal-header">
              <h2 className="graph-modal-title"></h2>
              <button 
                className="graph-modal-close" 
                onClick={closeGraphModal}
                aria-label=""
              >
                
              </button>
            </div>
            <div className="graph-modal-body">
              {/* GraphDisplay   */}
              <GraphDisplay 
                events={sortedEvents}
                isEmbedded={true}
              />
            </div>
            <div className="graph-modal-footer">
              <button 
                className="export-button"
                onClick={() => exportAsImage('png')}
                aria-label="PNG "
              >
                <span className="export-icon"></span>
                <span>PNG </span>
              </button>
              <button 
                className="export-button"
                onClick={() => exportAsImage('jpg')}
                aria-label="JPG "
              >
                <span className="export-icon"></span>
                <span>JPG </span>
              </button>
            </div>
          </div>
        </div>
      )}

      <style jsx>{`
        @keyframes saveSuccess {
          0% { transform: scale(1); }
          50% { transform: scale(1.05); }
          100% { transform: scale(1); }
        }
      `}</style>
    </Box>
  );
}

export default EventInputForm; 